/**
 * @param {function} func - 执行函数
 * @param {number} wait - 等待时间  默认 1000毫秒
 * @param {boolean} immediate - 是否立即执行 默认false
 * @param {number} stopTime - 停止触发后多少毫秒后 执行任务 默认300毫秒
 * @return {function}
 */
export const throttle = (func, wait = 1000, immediate = false, stopTime = 300) => {
  let last, timer
  return function(...arg) {
    const now = +new Date()
    if (last && now < last + wait) {
      clearTimeout(timer)
      timer = setTimeout(function() {
        func.apply(this, arg)
      }, stopTime)
    } else {
      if (!immediate) {
        if (last) {
          func.apply(this, arg)
        }
      } else {
        func.apply(this, arg)
      }
      last = now
    }
  }
}