<template>
  <el-tree-new
    ref="treeNew"
    v-on="$listeners"
    v-bind="$attrs"
    v-model="svalue"
    :settings="setting"
    :data="areaTree"
    @change="handdleChange"
  ></el-tree-new>
</template>
<script>
import { state, actions } from "~/store/index.js"
export default {
  name: "el-tree-dict",
  data() {
    return {
      list: [], //数据列表
      svalue: this.value,
      type: "", //请求字典类型
      setting: {
        value: state.dictSetting.treeSetting.idField, //指定选项的值为选项对象的某个属性值
        label: state.dictSetting.treeSetting.labelField, //指定选项的值为选项对象的某个属性值
        children: state.dictSetting.treeSetting.childrenField, //指定选项的值为选项对象的某个属性值
        leaf: state.dictSetting.treeSetting.leafField, //指定选项的叶子节点的标志位为选项对象的某个属性值
        level: state.dictSetting.treeSetting.levelField //层级字段名
      }
    }
  },
  props: {
    //是否返回键值
    keyValue: {
      type: Boolean,
      default: false
    },
    dictType: {
      type: [String, Object]
    }, //请求的码表值
    value: {}, //接受外部v-model传入的值
    disableObj: {
      type: Object
    },
    treeSetting: {
      type: Object,
      default: () => {
        return {}
      }
    },
    //过滤函数
    filterDataFun: {
      type: Function
    },
    //禁用函数
    disabledDataFun: {
      type: Function
    },
    //el-cascader配置
    props: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  computed: {
    dictCodes() {
      return state.dictCodes
    },
    dictSetting() {
      return state.dictSetting
    },
    isGettingAllDictTypes() {
      return state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return state.isGettingDictTypes
    },
    areaTree() {
      //深拷贝一下  避免影响到全局配置
      const setTreeSetting = JSON.parse(
        JSON.stringify(this.dictSetting.treeSetting)
      )
      const treeSetting = Object.assign(setTreeSetting, this.treeSetting)
      return this.$listToTree(this.list, treeSetting)
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type)) {
        this.getDictionary_option()
      }
    },
    //请求全部字典后 重新渲染页面
    isGettingAllDictTypes(val) {
      if (!val) {
        this.getDictionary_option()
      }
    },
    //判断下拉框的值是否有改变
    value(val) {
      this.svalue = val //②监听外部对props属性result的变更，并同步到组件内的data属性myResult中
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val) //③组件内对myResult变更后向外部发送事件通知
      }
    },
    props: {
      handler: function(val) {
        Object.assign(this.setting, val)
      },
      immediate: true
    }
  },
  mounted() {
    // this.svalue=this.value;//初始话下拉框的值
    this.getDictionary_option()
  },
  methods: {
    // 获取下拉框数据
    async getDictionary_option() {
      if (!this.dictType) {
        return
      }
      //获取该类型字典数据
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const {
          filters,
          filterType = this.dictSetting.treeSetting.idField,
          reverse = false
        } = this.dictType
        const filtersArr =
          typeof filters === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter(item => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter(item => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //使用函数过滤数据
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      } else {
        dictData = this.dictSetting.filterDataFun(dictData)
      }
      //使用函数禁用数据
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      } else {
        dictData = this.dictSetting.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const {
          disableValue,
          disableType = this.dictSetting.treeSetting.idField,
          reverse = false
        } = this.disableObj
        const disableList =
          typeof disableValue === "string"
            ? disableValue.split(",")
            : disableValue
        if (reverse) {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              !disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        }
      }

      this.list = dictData
    },
    // 下拉框点击事件
    handdleChange() {
      /* 子组件通过事件发射向父组件传递事件及参数，emit即为发射事件
        第一个参数为向父组件传递的事件名，第二个参数为向父组件传递的参数 */
      this.$emit("dictChange", this.svalue)
    },
    getTree() {
      return this.$refs.treeNew.getTree()
    }
  }
}
</script>