<template>
  <el-checkbox-group v-model="svalue" v-on="$listeners" v-bind="$attrs" @change="handdleChange">
    <el-checkbox-button
      v-for="item in list"
      :disabled="item.disabled"
      :key="item[dictSetting.format.value]"
      :label="item[dictSetting.format.value]"
      :border="border"
      :size="size"
      :name="name"
      :true-label="trueLabel"
      :false-label="falseLabel"
      :checked="checked"
      :indeterminate="indeterminate"
    >{{item[dictSetting.format.label]}}</el-checkbox-button>
  </el-checkbox-group>
</template>
<script>
import { state, actions } from "~/store/index.js"
export default {
  name: "el-checkbox-button-dict",
  data() {
    return {
      list: [], //数据列表
      svalue: this.value,
      type: "" //请求字典类型
    }
  },
  props: {
    //选中时的值
    trueLabel: {
      type: [String, Number],
      default: ""
    },
    //没有选中时的值
    falseLabel: {
      type: [String, Number],
      default: ""
    },
    //是否显示边框
    border: {
      type: Boolean,
      default: false
    },
    //Checkbox 的尺寸，仅在 border 为真时有效 medium / small / mini
    size: {
      type: String,
      default: ""
    },
    //原生 name 属性
    name: {
      type: String,
      default: ""
    },
    //设置 当前是否勾选
    checked: {
      type: Boolean,
      default: false
    },
    //设置 indeterminate 状态，只负责样式控制
    indeterminate: {
      type: Boolean,
      default: false
    },
    //是否返回键值
    keyValue: {
      type: Boolean,
      default: false
    },
    dictType: {
      type: [String, Object]
    }, //请求的码表值
    value: {}, //接受外部v-model传入的值
    //配置禁用选项
    disableObj: {
      type: Object
    },
    //过滤函数
    filterDataFun: {
      type: Function
    },
    //禁用函数
    disabledDataFun: {
      type: Function
    }
  },
  computed: {
    dictCodes() {
      return state.dictCodes
    },
    dictSetting() {
      return state.dictSetting
    },
    isGettingAllDictTypes() {
      return state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return state.isGettingDictTypes
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type)) {
        this.getDictionary_option()
      }
    },
    //请求全部字典后 重新渲染页面
    isGettingAllDictTypes(val) {
      if (!val) {
        this.getDictionary_option()
      }
    },
    //判断下拉框的值是否有改变
    value(val) {
      this.svalue = val //②监听外部对props属性result的变更，并同步到组件内的data属性myResult中
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val) //③组件内对myResult变更后向外部发送事件通知
      }
    }
  },
  mounted() {
    // this.svalue=this.value;//初始话下拉框的值
    this.getDictionary_option()
  },
  methods: {
    // 获取下拉框数据
    async getDictionary_option() {
      if (!this.dictType) {
        return
      }
      //获取该类型字典数据
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const {
          filters,
          filterType = this.dictSetting.format.value,
          reverse = false
        } = this.dictType
        const filtersArr =
          typeof filters === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter(item => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter(item => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //使用函数过滤数据
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      } else {
        dictData = this.dictSetting.filterDataFun(dictData)
      }
      //使用函数禁用数据
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      } else {
        dictData = this.dictSetting.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const {
          disableValue,
          disableType = this.dictSetting.format.value,
          reverse = false
        } = this.disableObj
        const disableList =
          typeof disableValue === "string"
            ? disableValue.split(",")
            : disableValue
        if (reverse) {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              !disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        }
      }
      this.list = dictData
    },
    // 下拉框点击事件
    handdleChange() {
      /* 子组件通过事件发射向父组件传递事件及参数，emit即为发射事件
        第一个参数为向父组件传递的事件名，第二个参数为向父组件传递的参数 */
      if (!this.keyValue) {
        this.$emit("dictChange", this.svalue)
        return
      }
      this.$emit(
        "dictChange",
        this.list.filter(item => {
          return this.svalue.includes(item[this.dictSetting.format.value])
        })
      )
    }
  }
}
</script>