<template>
  <el-link :type="judgeType" v-on="$listeners" v-bind="$attrs">{{value | getLabelByCode(dictType)}}</el-link>
</template>

<script>
export default {
  name: "el-link-dict",
  props: {
    value: {

    },
    dictType: {
      type: [String]
    }, //请求的码表值
    judgeTypeFun: {
      type: Function,
      default: () => {

      }
    },
    type: {
      type: String,
      default: "primary"
    }
  },
  computed: {
    judgeType() {
      if (!this.judgeTypeFun) {
        return this.type
      }
      return this.judgeTypeFun(this.value) || this.type
    }
  }
}
</script>

