/**
 * @param {function} func - 执行函数
 * @param {number} wait - 等待时间
 * @param {boolean} immediate - 是否立即执行
 * @return {function}
 */

export const debounce = (func, wait = 300, immediate = false) => {
  var timer = null
  return function(...arg) {
    if (timer !== null) {
      clearTimeout(timer)
    } else if (immediate) {
      func.apply(this, arg)
    }
    timer = setTimeout(() => {
      func.apply(this, arg)
      timer = null
    }, wait)
  }
}