<!--
 * @Description: 表格字典
 * @Version: 0.1
 * @Autor: Chenyt
-->
<template>
  <el-table-column v-on="$listeners" v-bind="$attrs" show-overflow-tooltip>
    <template slot-scope="scope">
      <div v-if="dictType">
        {{ scope.row[$attrs.prop] | getLabelByCodes(dictType, defaultValue, formatFun, spacer) }}
      </div>
       <div v-else-if="dictTreeType">
        {{ scope.row[$attrs.prop] | getTreeLabelByCodes(dictTreeType, defaultValue, formatFun, treeSetting, formatFunOut, spacer) }}
      </div>
      <div v-else-if="dateFormat">
        {{ scope.row[$attrs.prop] | dayFormat(dateFormat, defaultValue) }}
      </div>
      <div v-else-if="desensitization">
        {{ scope.row[$attrs.prop] | desensitization(desensitization, defaultValue, desensitizationEnd, desensitizationPlaceholder) }}
      </div>
      <div v-else>
        {{ scope.row[$attrs.prop] || defaultValue }}
      </div>
    </template>
  </el-table-column>
</template>
<script>
export default {
  name: "el-table-column-dict",
  props: {
    dictType: String, //字典类型
    dictTreeType: String, //树形数据字典类型
    treeSetting: Object, //树形配置
    dateFormat: String, //日期格式化
    desensitization: [String, Number], //脱敏类型或者 展示前面几个数
    defaultValue: {
      type: String, //缺省时展示
      default: ""
    },
    spacer: String, // 间隔
    formatFun: Function, // 格式化展示内容  接收一个  arr数组  树形数据的话 是格式化里面的内容
    formatFunOut: Function, // 格式化 最外层 展示形式 接收  arr数组
    desensitizationEnd: Number, //展示后面几个
    desensitizationPlaceholder: Number //中间占位符个数
  }
}
</script>
