<script>
export default {
  name: "BuButton",
  props: {
    type: {
      type: String,
      default: "normal"
    },
    text: {
      type: String,
      default: "button"
    }
  },
  render(h){
    /*
            h 类似于 createElement， 接受2个参数
            1 - 元素
            2 - 选项
         */
    return h("button", {
      // 相当于 v-bind:class
      class: {
        btn: true,
        "btn-success": this.type === "success",
        "btn-danger": this.type === "danger",
        "btn-warning": this.type === "warning",
        "btn-normal": this.type === "normal"
      },
      domProps: {
        innerText: this.text || "默认"
      },
      on: {
        click: this.handleClick
      }
    })
  },
  methods: {
    handleClick(){
      this.$emit("click")
    }
  }
}
</script>

<style scoped>
.btn{
    width: 100px;
    height:40px;
    line-height:40px;
    border:0px;
    border-radius:5px;
    color:#ffff;
}
.btn-success{
    background:#2ecc71;
}
.btn-danger{
    background:#e74c3c;
}
.btn-warning{
    background:#f39c12;
}
.btn-normal{
    background:#bdc3c7;
}
</style>